.text-image {
    padding: 44px 0;
    background: #FFFFFF;
        
    @media screen and (max-width: 768px) {
        padding: 47px 0 17px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        
        @media screen and (max-width: 768px) {
            flex-direction: column-reverse;
        }
    }

    &__image {
        display: block;
        width: 49%;
        margin-right: 11%;
        
        @media screen and (max-width: 768px) {
            width: 500px;
            max-width: 100%;
            margin-right: 0;
        }
    }

    &__text {
        width: 41%;
        
        @media screen and (max-width: 768px) {
            width: 100%;
            margin-bottom: 40px;
        }
    }

    &__title {
        margin-bottom: 6px;
        font-weight: 500;
        font-size: 16px;
        line-height: 136%;
        color: #737373;

        @media screen and (max-width: 1200px) {
            font-size: 12px;
        }
    }

    &__desc {
        margin-bottom: 40px;
        font-weight: 600;
        font-size: 50px;
        line-height: 106%;
        color: #000000;

        @media screen and (max-width: 1200px) {
            font-size: 36px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto;
        gap: 40px 20px;
    }

    &__item-title {
        margin-bottom: 2px;
        font-weight: 400;
        font-size: 45px;
        line-height: 106%;
        color: #000000;

        @media screen and (max-width: 1200px) {
            font-size: 30px;
        }
    }

    &__item-text {
        font-weight: 500;
        font-size: 14px;
        line-height: 136%;
        color: #000000;
    }
}